
$primary-color:#fff;
$secondary-color:#044DC3;
$mobile-screen:445px;
$tablet-screen:768px;
$medium-screen:1024px;
$large-screen:1440px;
$x-large-screen:1920px;

body{
    margin: 0;
}

@font-face {
    font-family: 'Source Sans Pro';
    src: local('Source Sans Pro'), url(fonts/SourceSansPro-Regular.ttf) format('tff');
  }

.body{
    display: flex;
    justify-content: center;
}

.header{
    display: flex;
    width: 100%;
    background: $primary-color;
    @media (max-width: $mobile-screen) {
        padding: 12px
    }
    @media (min-width: $tablet-screen) {
        padding: 20px 80px;
    }
    @media (min-width: $medium-screen) {
        padding: 20px 120px;
    }
    @media (min-width: $x-large-screen) {
        padding: 20px 220px;
    }
    .bucket-grid{
        position: relative;

        .bucket{
            background: #044DC3 0% 0% no-repeat padding-box;
            border-radius: 28px;
            opacity: 1;
            font-family: 'Source Sans Pro', regular;
            text-transform: none;
            font-size: 12px;
            padding: 10px 25px;
            @media (max-width: $mobile-screen) {
                margin-top: 16px
            }
            .bucket__icon{
                margin-right: 4px;
            }
        }
        .bucket-count-grid{
            width: 25px;
            height: 25px;
            border-radius: 50%;
            position: absolute;
            background: #FF5B4D;
            z-index: 2;
            right: -5px;
            top: -5px;
            display: flex;
            align-items: center;
            justify-content: center;

            .bucket-count-text{
                font-size: 16px;
                color: $primary-color;
                font-weight: 600;
            }
        }
    }
    .bucket-text-grid{
        position: absolute;
        right: 0;
        padding:16px;
        background: #F54257 0% 0% no-repeat padding-box;
        width: 343px;
        margin-top: 10px;
        border-radius: 16px;

        .bucket-text{
            font-size: 15px;
            color: $primary-color;
        }
    }
    .header__left{
        display: flex;
        flex-wrap: nowrap;
    }
    .search {
        position: relative;
        width:100%;
        @media (max-width: $tablet-screen) {
            max-width: 80px;
        }
        max-width: 870px;
        margin-left: 80px;
        
        .search-icon {
            padding-left: 25px;
            height: 100%;
            position: absolute;
            z-index: 99999;
            pointer-events: none;
            display: flex;
            align-items: center;
            justify-content: center;
            color: #7c7c7c;
        }
        .search-input {
            padding: 16px 32px;
            padding-left:50px;
            height:10px;
            font-size:16px;
            font-family: 'Source Sans Pro';
            border: 2px solid #EDF1F2;
            border-radius: 28px;
            outline:none;
            width: 100%;
            background-color: #F7F7F7;
            max-width: 500px;
            color: #555555;
            @media (min-width: $x-large-screen) {
                max-width: 784px;
            }
            @media (max-width: $mobile-screen) {
                max-width: 70px;
            }
        }
    }
}

.blue-field{
    display: flex;
    width: 100%;
    background: $secondary-color;
    background-image: url("./static/img/blue-image.png");
    @media (max-width: $mobile-screen) {
        padding: 12px
    }
    @media (min-width: $tablet-screen) {
        padding: 20px 80px;
    }
    @media (min-width: $medium-screen) {
        padding: 20px 120px;
    }
    @media (min-width: $x-large-screen) {
        padding: 20px 220px;
    }
    .blue-field__text{
        color:#fff;
        font-size: 35px;
        font-family: 'Source Sans Pro', regular;
    }
}

.main-grid{
    width: 100%;
    background: $primary-color;
    @media (max-width: $mobile-screen) {
        padding: 12px
    }
    @media (min-width: $tablet-screen) {
        padding: 10px 80px;
    }
    @media (min-width: $medium-screen) {
        padding: 10px 120px;
    }
    @media (min-width: $x-large-screen) {
        padding: 10px 220px;
    }
    .breadcrumb-text{
        font-size: 13px;
        font-family: 'Source Sans Pro', regular;
    }
    .breadcrumb-icon{
        font-size: 20px;
    }
    .category-title-grid{
        margin-top: 10px;

        .category-icon{
            margin-right: 5px;
        }

        .category-text{
            font-size: 24px;
            font-family: 'Source Sans Pro', regular;
            color: $secondary-color;
        }
    }
    .category-item-grid{
        margin:-10px;
        margin-top: 0px;

        .category-item{
            padding: 8px;
            transition: 0.5s;

            .category-item__box{
                width: 195px;
                height: 50px;
                border: 1px solid #E2E7E9;
                border-radius: 10px;
                display: flex;
                align-items: center;
                justify-content: center;
                cursor: pointer;
                .category-item__text{
                    font-family: 'Source Sans Pro', regular;
                    font-size: 18px;
                    font-weight: 500;
                    color: #707070;
                }
            }
        }
    }
    .product-grid{
        margin-top: 30px;
        .product-icon{
            margin-right: 10px;
        }
        .product-title{
            font-size: 20px;
            color: #707070;
            font-weight: 300;
        }
    }
    .product-container{
        margin:-12px;
        margin-top:0px;
        .category-item{
            padding: 12px;
            .category-item__box{
                padding: 15px;
                width: 272px;
                height: 400px;
                background: #FFFFFF 0% 0% no-repeat padding-box;
                border: 1px solid #E2E7E9;
                border-radius: 10px;
                
                .category-item__title{
                    color: #555555;
                    font-size: 15px;
                    font-family: 'Source Sans Pro', regular;
                    font-weight: 600;
                    min-height: 42px;
                }
                .category-item__status{
                    color: #51B549;
                    font-size: 15px;
                    font-family: 'Source Sans Pro', regular;
                    font-weight: 600;
                    min-height: 20px;
                }
                .category-item__price{
                    color: #000000;
                    font-size: 15px;
                    font-family: 'Source Sans Pro', regular;
                    font-weight: 600;
                }
                .category-item__bucket{
                    border: 1px solid $secondary-color;
                    border-radius: 22px;
                    width: 242px;
                    height: 40px;
                    font-size: 15px;
                    font-family: 'Source Sans Pro', regular;
                    color: $secondary-color;
                    font-weight: 600;
                    text-transform: none;
                }
                .category-item__bucket-added{
                    border: 1px solid #E2E7E9;
                    border-radius: 22px;
                    width: 242px;
                    height: 40px;
                    font-size: 15px;
                    font-family: 'Source Sans Pro', regular;
                    color: $secondary-color;
                    font-weight: 600;
                    text-transform: none;
                    padding: 3px;
                }
                .category-item__minus-icon{
                    width: 30px;
                    height: 30px;
                    border-radius: 50%;
                    display: flex;
                    align-items: center;
                    justify-self: center;
                    background: #E8F1FF;
                    cursor: pointer;
                }
                .category-item__minus-icon-text{
                    color: $secondary-color;
                    font-size: 45px;
                    width: 100%;
                    text-align: center;
                    margin-bottom: 5px;
                }
                .category-item__count{
                    display: flex;
                    align-items: center;
                    justify-self: center;
                }
                .category-item__count-text{
                    width: 100%;
                    font-family: 'Source Sans Pro', regular;
                    font-size: 22px;
                    font-weight: bold;
                    color: #555555;
                }
            }
        }
    }
}

.info-grid{
    width: 100%;
    background: $primary-color;
    @media (max-width: $mobile-screen) {
        padding: 12px
    }
    @media (min-width: $tablet-screen) {
        padding: 10px 80px;
    }
    @media (min-width: $medium-screen) {
        padding: 10px 120px;
    }
    @media (min-width: $x-large-screen) {
        padding: 10px 220px;
    }
    .pink-info{
        background: #FFEAE8 0% 0% no-repeat padding-box;
        border-radius: 10px;
        padding: 10px;
        flex-wrap: nowrap;
    }
    .blue-info{
        background: #E8F1FF 0% 0% no-repeat padding-box;
        border-radius: 10px;
        padding: 10px;
        flex-wrap: nowrap;
    }
    .info-content {
        .info-content__text {
            padding-left: 15px;
            padding-top: 5px;
            font-size: 16px;
            font-family: 'Source Sans Pro', regular;
            color: #707070;

            @media (min-width: $x-large-screen) {
                padding-left: 35px;
                padding-top: 15px;
                font-size: 24px;
            }
        }
        .info-content__button{
            background: #FFFFFF 0% 0% no-repeat padding-box;
            border-radius: 15px;
            font-weight: 600;
            font-size: 13px;
            color: #E64E41;
            margin-top: 10px;
            margin-left: 15px;
            padding: 5px 15px;
            text-transform: none;
            @media (min-width: $x-large-screen) {
                margin-left: 35px;
            }
        }
    }
}
.footer{
    background: #F2F2F2 0% 0% no-repeat padding-box;
    margin-top: 100px;
    
    .footer-grid{
        width: 100%;
        @media (max-width: $mobile-screen) {
            padding: 12px
        }
        @media (min-width: $tablet-screen) {
            padding: 10px 80px;
        }
        @media (min-width: $medium-screen) {
            padding: 150px 120px 60px 120px;
        }
        @media (min-width: $x-large-screen) {
            padding: 150px 220px 60px 120px;
        }
        
        .white-grid{
            background: #FFFFFF 0% 0% no-repeat padding-box;
            border-radius: 111px;
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
        }
        .phone-photo{
            margin-top: -150px;
        }
        .qr-title{
            font-size: 18px;
            color: #000000;
            font-family: 'Source Sans Pro', regular;
            font-weight: 600;
        }
        .app-grid{
            width: max-content;
            margin-left: 10px;
            margin-top: 25px;
        }
        .apple-img{
            max-width: 175px;
        }
        .footer-left{
            @media (min-width: $tablet-screen) {
                border-right: 1px dashed #9B9B9B;
            }
            padding-bottom: 50px;
        }
        .footer-right{
            display: flex;
            flex-wrap: wrap;
            @media (min-width: $tablet-screen) {
                padding-left: 30px;
            }
            .footer-right__item{
                text-align: center;
            }

            .footer-right__title{
                color: #044DC3;
                font-family: 'Source Sans Pro', regular;
                font-size: 18px;
                margin-bottom: 10px;
            }
            .footer-right__text{
                color: #000;
                font-family: 'Source Sans Pro', regular;
                font-size: 15px;
                margin-bottom: 10px;
            }
        }
        .social-grid{
            margin-top: 35px;
            .social-item{
                margin-right: 30px;
            }
        }
        .privacy-text{
            margin-top: 24px;
            color: #000000;
            font-family: 'Source Sans Pro', regular;
            font-size: 15px;
        }
    }
    .bottom-part{
        height: 60px;
        background: #E6E6E6 0% 0% no-repeat padding-box;
    }
}